.client {
  background-color: $_secondary_dark;
}

.client__container {
  padding: 82px 16px 106px;
}

.client__title {
  margin: 0;
  padding: 0;
  font-family: $_f__common;
  font-style: normal;
  font-weight: 400;
  font-size: 35px;
  line-height: 69px;
  color: $_main_light;
  letter-spacing: -3px;
}

.client__text {
  margin: 0 0 82px;
  padding: 0;
  letter-spacing: -1px;
  font-family: $_f__common;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: $_main_light;
}

.client__image-wrapper {
display: flex;
flex-wrap: wrap;
justify-content: space-between;
gap: 30px;
}

.client__image {
  width: 75px;
  height: 75px;
  display: block;
}


@include media-tablet {

  .client__container
  {
  margin:auto;}
  .client__title
  {
    font-size: 50px;
    line-height: 69px;
    letter-spacing: -3px;
  }
  .client__text
  {
    padding-top:31px;
    font-weight: 400;
    font-size: 24px;
    line-height: 140%;
    letter-spacing: -1px;
    width:464px;
  }

}