.construction {
  background-color: $_main_light;
  overflow: hidden;
}

.construction__container {
  position: relative;
  padding: 12px 16px 50px;
}

.construction__slogan {
  margin: 0 0 43px;
  padding: 0;
  font-family: $_f__common;
  font-style: normal;
  font-weight: 300;
  font-size: 55px;
  line-height: 58px;
  color: $_main_dark;
  letter-spacing: -3px;

}

.construction__text {
position: relative;
margin: 0 0 73px;
padding: 0;
font-family: $_f__common;
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 24px;
color: $_main_dark;
letter-spacing: -0.5px;

&::before {
position: absolute;
content: '';
left: calc(50% - 700px);
bottom: -50px;
width: 1400px;
height: 1400px;
background-image: url("data:image/svg+xml, %3Csvg fill='none' stroke='%235D9F93' xmlns='http://www.w3.org/2000/svg' id='icon-plus' viewBox='0.9 0.9 24.2 24.2'%3E%3Cg stroke-width='0.07' fill-rule='evenodd'%3E%3Cg id='Learn' transform='translate(-249.000000, -3678.000000)'%3E%3Cg id='Group-6' transform='translate(250.000000, 3679.000000)'%3E%3Cpath d='M24,24 L12.0006916,24 C5.40141779,24 0,18.5999654 0,12.0006916 C0,5.40141779 5.40141779,0 12.0006916,0 C18.5999654,0 24,5.40141779 24,12.0006916 L24,24 Z' vector-effect='non-scaling-stroke' stroke-width='2px'/%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
background-repeat: no-repeat;
background-position: top left;
transform: rotate(180deg);
}
}

.construction__title {
  margin: 0 0 22px;
  padding: 0;
  font-family: $_f__common;
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 52px;
  color: $_main_dark;
}

.construction__list {
  @include list-style-reset;
}

.construction__item {
  padding: 17px 0;
  border-bottom: 1px solid #5D9F93;
  letter-spacing: -0.5px;
}

.construction__item:last-child {
  border-bottom: none;
}

@include media-tablet {
.construction__slogan
{
  width:630px;
  font-size: 85px;
  line-height: 100px;
  letter-spacing: -3px;
}
.construction__text
{
  width:55%;
  margin-left: auto;
  margin-right: 0;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -0.5px;
  padding-right:10px;

}
.construction__text::before {
width: min(1020px, 120vw);
height: min(1020px, 120vw);
transform:none;
left:auto;
right:0px;
font-size: 18px;
line-height: 24px;
letter-spacing: -0.5px;
}
.construction__list
{
columns: 2;
}
}

@include media-desktop {
.construction__slogan
{
  width:710px;
  font-size: 96px;
  line-height: 100px;
  letter-spacing: -3px;
}
.construction__text
{
  width:55%;
  font-size:18px;
  line-height:24px;
  width:228px;
  margin-left: auto;
  margin-right: 0;
  letter-spacing: -0.5px;

}
.construction__text::before {
  width: 1184px;
  height: 1184px;
  transform:none;
  left:auto;
    left: -180px;
    right: auto;
    bottom: -120px;}
  .construction__container
  {
    display: grid;
    justify-items: start;
    grid-template-columns: 2fr 1fr;
    padding-top:62px;
  }
  .construction__title, .construction__list
  {
    width: 100%;
    grid-column: 1/3;
  }
}